.taskDetails {
  overflow-y: scroll;
  height: 70vh;
  width: 70vw;
}

.taskDetails::-webkit-scrollbar {
  background-color: var(--palette_bg);
}

.taskDetails::-webkit-scrollbar {
  width: 12px; /* ширина всей полосы прокрутки */
}

.taskDetails::-webkit-scrollbar-track {
  background: var(--palette_bg); /* цвет зоны отслеживания */
}

.taskDetails::-webkit-scrollbar-thumb {
  background-color: var(--palette_bg); /* цвет бегунка */
  border-radius: 20px; /* округлось бегунка */
  border: 3px solid white; /* отступ вокруг бегунка */
}

.description {
  min-height: 200px;
  padding: 10px;
  // background-color: black;
  color: var(--palette_text);

  border-radius: 3px;
  border: 3px solid var(--palette_border); /* отступ вокруг бегунка */
}

.descriptionTitle {
  margin: 0;
}

.descriptionText {
  margin: 0;
  word-break: break-all;
}
